*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

html {
  box-sizing: border-box;
}

div {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  padding: 0;
  margin: 0;
  color: inherit;
  text-decoration: none;
}

img {
  object-fit: cover;
}

a,
button,
input,
textarea {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

body {
  font-size: 24px;
  line-height: 32px;
  color: #181818;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  padding-top: 70px;
  overflow: hidden;
}

.container {
  max-width: 1259px;
  padding: 0 15px;
  margin: 0 auto;
}

.section-outer {
  margin-bottom: 120px;
}
.section-outer--small {
  margin-bottom: 100px;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
  font-size: 55px;
  line-height: 55px;
  text-align: center;
  letter-spacing: 0.01em;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 300;
}
.title::before, .title::after {
  width: 60vw;
  height: 2px;
  margin: 0 60px;
  content: "";
  background: #dfd7d3;
}
.title::before {
  margin-left: -20vw;
}
.title::after {
  margin-right: -20vw;
}
.title__text {
  width: 100%;
  max-width: fit-content;
}

.btn {
  display: block;
  max-width: 100%;
  padding: 24px 44px;
  cursor: pointer;
  background: none;
  border: none;
}
.btn .btn-text {
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  font-family: "Circle", sans-serif;
  font-weight: 400;
}
.btn-no-bg {
  position: relative;
  min-width: 231px;
  color: #fff;
  border: 2px solid #dbdbdb;
  border-right: none;
  transition: all 0.4s ease-in-out;
}
.btn-no-bg .btn-text {
  position: relative;
}
.btn-no-bg .btn-text::before {
  position: absolute;
  top: 50%;
  bottom: 50%;
  left: calc(100% - -18px);
  width: 66px;
  height: 10px;
  content: "";
  background: url("data:image/svg+xml;charset=UTF-8,%3csvg width='66' height='10' viewBox='0 0 66 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M61.2071 0.292847L65.9142 4.99995L61.2071 9.70706L59.7929 8.29285L62.0858 5.99995H0V3.99995H62.0858L59.7929 1.70706L61.2071 0.292847Z' fill='%23DFD7D3'/%3e%3c/svg%3e ") no-repeat;
  transition: all 0.4s ease-in-out;
  transform: translateY(-50%);
}
.btn-no-bg::before, .btn-no-bg::after {
  position: absolute;
  right: 0;
  width: 2px;
  height: 30%;
  content: "";
  background-color: #dbdbdb;
  transition: all 0.4s ease-in-out;
}
.btn-no-bg::before {
  top: 0;
}
.btn-no-bg::after {
  bottom: 0;
}
.btn-no-bg:hover {
  border-color: #ca6d38;
}
.btn-no-bg:hover::before, .btn-no-bg:hover::after {
  background-color: #ca6d38;
}
.btn-no-bg:hover .btn-text::before {
  left: calc(100% - -30px);
}
.btn-no-bg:active {
  border-color: #dbdbdb;
}
.btn-no-bg:active::before, .btn-no-bg:active::after {
  background-color: #dbdbdb;
}
.btn-no-bg:active .btn-text::before {
  left: calc(100% - -10px);
}
.btn-bg {
  color: #181818;
  background-color: #dfd7d3;
  transition: all 0.4s ease-in-out;
}
.btn-bg:hover {
  box-shadow: 0 0 30px rgba(223, 215, 211, 0.2);
}
.btn-bg:active {
  box-shadow: 0 0 15px rgba(223, 215, 211, 0.1);
}
.btn-standart {
  border: 2px solid #dfd7d3;
  transition: all 0.4s ease-in-out;
}
.btn-standart .btn-text {
  color: #454545;
}
.btn-standart:hover {
  border-color: #ca6d38;
}
.btn-standart:active {
  opacity: 0.8;
}

.logo__img {
  width: 230px;
  height: 41px;
  object-fit: contain;
}

.menu__list {
  display: flex;
}
.menu__list-item + .menu__list-item {
  margin-left: 34px;
}
.menu__list-link {
  font-size: 20px;
  line-height: 25px;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 300;
}
@media (any-hover: hover) {
  .menu__list-link {
    transition: all 0.4s ease-in-out;
  }
  .menu__list-link:hover {
    color: #ca6d38;
  }
}

.burger-btn {
  position: relative;
  display: none;
  width: 31px;
  height: 12px;
  margin-left: auto;
  cursor: pointer;
  cursor: pointer;
  background: none;
  border: none;
}
.burger-btn__line {
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 50%;
  width: 100%;
  height: 2px;
  background-color: #ca6d38;
  transition: all 0.4s ease-in-out;
  transform: translateY(-50%);
}
.burger-btn::before, .burger-btn::after {
  position: absolute;
  right: 0;
  height: 2px;
  content: "";
  background-color: #ca6d38;
  transition: all 0.4s ease-in-out;
}
.burger-btn::before {
  top: 0;
  width: 100%;
}
.burger-btn::after {
  bottom: 0;
  width: 70%;
}
@media (any-hover: hover) {
  .burger-btn:hover::after {
    width: 100%;
  }
}

.contact-phone {
  position: relative;
  display: flex;
  align-items: center;
}
.contact-phone__img {
  position: absolute;
  top: -2px;
  width: 15px;
  height: 15px;
}
.contact-phone__text {
  padding-left: 25px;
  line-height: 25px;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 400;
}
@media (any-hover: hover) {
  .contact-phone {
    transition: all 0.4s ease-in-out;
  }
  .contact-phone:hover svg {
    fill: #ca6d38;
  }
  .contact-phone:hover {
    color: #ca6d38;
  }
}

.social-item {
  position: relative;
  display: flex;
}
.social-item__img {
  position: absolute;
  top: -30%;
  left: 0;
}
.social-item__img svg {
  width: 15px;
  height: 15px;
}
.social-item__img svg path {
  fill: #fff;
  transition: all 0.4s ease-in-out;
}
.social-item__text {
  padding-left: 30px;
  font-size: 22px;
  line-height: 25px;
  color: #f3f1ef;
  transition: all 0.4s ease-in-out;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 400;
}
@media (any-hover: hover) {
  .social-item__text:hover {
    color: #ca6d38;
  }
  .social-item:hover path {
    fill: #ca6d38;
  }
}

.slider-navigation {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 556px;
}

.slider-button {
  display: inline;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}
.slider-button svg {
  width: 91px;
  height: 11px;
  fill: #ca6d38;
}
.slider-button path {
  transition: all 0.2s ease-in-out;
}
.slider-button:hover path {
  box-shadow: 0 1px 4px rgba(69, 69, 69, 0.25);
}
.slider-button:active path {
  box-shadow: 0 1px 4px rgba(69, 69, 69, 0.25);
  fill: rgba(202, 109, 56, 0.8);
}
.slider-button-prev svg {
  transform: rotate(-180deg);
}

.swiper-button-disabled {
  cursor: auto;
  opacity: 0.3;
}

.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background: none;
  border: 1px solid #dfd7d3;
  opacity: 1;
  transition: all 0.85s ease-in-out;
}
.swiper-pagination-bullet + .swiper-pagination-bullet {
  margin-left: 10px;
}
.swiper-pagination-bullet-active {
  background: #ca6d38;
  box-shadow: 0 1px 4px rgba(69, 69, 69, 0.25);
}

.swiper-slide-next,
.swiper-slide-prev {
  cursor: pointer;
}

.tabs-triggers {
  display: flex;
  justify-content: center;
  margin-bottom: 45px;
}
.tabs-triggers__item {
  position: relative;
  font-size: 30px;
  line-height: 32px;
  color: #454545;
  transition: all 0.4s ease-in-out;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.tabs-triggers__item::before {
  position: absolute;
  right: 0;
  bottom: -5px;
  left: auto;
  width: 0;
  height: 2px;
  content: "";
  background-color: #ca6d38;
  transition: all 0.4s ease-in-out;
}
.tabs-triggers__item + .tabs-triggers__item {
  margin-left: 40px;
}
.tabs-triggers__item--active {
  color: #ca6d38;
}
.tabs-triggers__item--active::before {
  right: auto;
  left: 0;
  width: 100%;
}
.tabs-triggers__item:hover {
  color: #ca6d38;
}
.tabs-content__item {
  display: none;
}
.tabs-content__item--active {
  display: block;
}

.filter-items {
  display: flex;
  justify-content: center;
  margin-bottom: 94px;
}
.filter-item {
  position: relative;
  font-size: 30px;
  line-height: 32px;
  color: #454545;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.filter-item::before {
  position: absolute;
  right: 0;
  bottom: -5px;
  left: auto;
  width: 0;
  height: 2px;
  content: "";
  background-color: #ca6d38;
  transition: all 0.4s ease-in-out;
}
.filter-item + .filter-item {
  margin-left: 40px;
}
.filter-item--active {
  color: #ca6d38;
}
.filter-item--active::before {
  right: auto;
  left: 0;
  width: 100%;
}

.breadcrumbs__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.breadcrumbs__list-item {
  margin-top: 10px;
  margin-right: 20px;
  font-size: 16px;
  line-height: 24px;
  color: #dfd7d3;
  transition: all 0.4s ease-in-out;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.breadcrumbs__list-item:hover a {
  color: #ca6d38;
}
.breadcrumbs__list-item:hover a::after {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='36' height='6' viewBox='0 0 36 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M33.8295 2.34569L32.2464 0.691389L32.9688 0L35.6921 2.84569L32.9688 5.69139L32.2464 5L33.8295 3.34569H0V2.34569H33.8295Z' fill='%23ca6d38'/%3e%3c/svg%3e ");
}
.breadcrumbs__list-item:last-child {
  margin-right: 0;
}
.breadcrumbs__list-item a {
  position: relative;
  padding-right: 56px;
  transition: all 0.4s ease-in-out;
}
.breadcrumbs__list-item a::after {
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 50%;
  width: 36px;
  height: 6px;
  content: "";
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='36' height='6' viewBox='0 0 36 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M33.8295 2.34569L32.2464 0.691389L32.9688 0L35.6921 2.84569L32.9688 5.69139L32.2464 5L33.8295 3.34569H0V2.34569H33.8295Z' fill='%23DFD7D3'/%3e%3c/svg%3e ");
  transition: all 0.4s ease-in-out;
  transform: translateY(-50%);
}

.pagination {
  margin-top: 60px;
}
.pagination-list {
  display: flex;
  justify-content: center;
}
.pagination-list__item {
  width: 35px;
  height: 35px;
  padding: 6px 12px;
  margin: 0 7.5px;
  font-size: 24px;
  line-height: 24px;
  white-space: nowrap;
  transition: all 0.4s ease-in-out;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.pagination-list__item:hover {
  color: #ca6d38;
}
